___CSS_LOADER_EXPORT___.push([module.id, `body {
  color: rgb(255, 255, 255);
  margin: 0;
}

body::before {
  content: " ";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  z-index: -1;
  background-image:
    linear-gradient(115deg, rgba(54, 178, 235, 0.5), rgba(9, 86, 228, 0.5)),
    url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-repeat: no-repeat;
  background-position: center;
}

h1 {
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  text-align: center;
  margin: 1em auto;
  padding: 0;
  font-size: 3em;
}

p {
  font-family: 'Italianno', cursive;
  font-weight: 300;
  text-align: center;
  margin: 5px auto 50px auto;
  padding: 0;
  font-size: 1.5em;
}

#survey-form {
  width: 60%;
  margin: 0 auto;
  background-color: rgba(3, 3, 17, 0.5);
  padding: 2em;
  border-radius: 0.5em;
  max-width: 800px;
  min-width: 200px;
}

fieldset {
  border: none;
  margin: 0;
  padding: auto;
}

input,
select,
textarea {
  display: block;
  width: 100%;
  padding: 0.2em;
  margin: 10px 0 0 0;
  border: none;
  min-height: 1em;
  border-radius: 0.5em;
  font-size: 1.2em;
  font-family: 'Italianno', cursive;
  color: rgb(244, 244, 244);
  background-color: rgba(40, 45, 133, 0.5);
}

label {
  display: block;
  margin: 0.5em 0;
  font-size: 1.2em;
  font-family: 'Italianno', cursive;
  color: rgb(244, 244, 244);
  font-weight: bolder;
}

.inline {
  display: inline-block;
  width: unset;
  margin: 0 0.3em;
  padding: auto;
  vertical-align: middle;
}

input[type="submit"] {
  display: block;
  width: 25vw;
  margin: auto;
  padding: auto;
  color: rgb(0, 0, 0);
  background-color: rgb(195, 185, 174);
  border-color: rgb(0, 0, 0);
  font-weight: bolder;
}
